import { observable, configure, action, decorate } from 'mobx';

configure({ enforceActions: 'observed' });

class MenuStore {
	zoom = 100;

	setZoom = (value) => {
		this.zoom = value;
		console.log(this.zoom);
	}
};

decorate(MenuStore, {
	zoom: observable,
  direction: observable,
  setZoom: action,
  changeDirection: action
});

export default MenuStore;