import React, { Component, Fragment } from 'react';

import { observer, inject } from 'mobx-react';
import InputRange from 'react-input-range';

import '../../../node_modules/react-input-range/lib/css/index.css';
import './zoom.css';

import icons from "../../icons/icons.svg";
import iconClose from "../../icons/icon_close.svg";

@inject("ZoomStore", "AppStore", "GraphStore")

@observer class Zoom extends Component {

  render() {

    const zoomStore = this.props.ZoomStore;
    const appStore = this.props.AppStore;
    const graphStore = this.props.GraphStore;
    
  	return (
			<Fragment>
				<div className={ `zoom__map ${appStore.theme} ${zoomStore.mapIsOpen ? 'map__open' : ''}` }>
					<div id="myOverviewDiv" className="map__overview"></div>
					<div className="map__close" onClick={zoomStore.closeMap}>
			      <img src={iconClose} alt="" />
			    </div>
				</div>
		    <div className={ `zoom ${appStore.theme} ${zoomStore.mapIsOpen ? 'map__open' : ''}` }>
		    	<div className="zoom__minus" onClick={ () => graphStore.setZoom(graphStore.zoom - 5) }>
			      <svg width="40" height="40">
	            <use xlinkHref={ `${icons}#minus` }></use>
	         	</svg>
			    </div>
			    <div className="zoom__scale">
			      <InputRange
			        maxValue={200}
			        minValue={1}
			        value={graphStore.zoom}
			        onChange={ (value) => graphStore.setZoom(value) } />
			    </div>
			    <div className="zoom__plus" onClick={ () => graphStore.setZoom(graphStore.zoom + 5) }>  
			      <svg width="40" height="40">
	            <use xlinkHref={ `${icons}#plus` }></use>
	         	</svg>
			    </div>
			    <div className="zoom__mapicon" onClick={zoomStore.openMap}>
			      <svg width="40" height="40">
	            <use xlinkHref={ `${icons}#map` }></use>
	         	</svg>
			    </div>
			  </div>
		  </Fragment>
		);
  }
}

export default Zoom;
