import { observable, computed, configure, action, decorate } from 'mobx';

import GraphStore from "./GraphStore.js";
import MenuStore from "./MenuStore.js";
import RightMenuStore from "./RightMenuStore.js";
import InfoCardStore from "./InfoCardStore.js";
import RiskCardStore from "./RiskCardStore.js";
import ZoomStore from "./ZoomStore.js";

configure({ enforceActions: 'observed' });

class AppStore {
  MenuStore = new MenuStore();
  RightMenuStore = new RightMenuStore();
  InfoCardStore = new InfoCardStore();
  RiskCardStore = new RiskCardStore(this);
  ZoomStore = new ZoomStore();
  GraphStore = new GraphStore(this, this.RiskCardStore, this.InfoCardStore);

  theme = 'dark';

  changeTheme = () => {
    this.theme === 'light' ? this.theme = 'dark' : this.theme = 'light';
  }

  get nodeTextColor(){
    return this.theme === 'dark' ? '#ffffff' : '#2b3250';
  }

  get defaultStrokeColor(){
    return this.theme === 'dark' ? '#5a6487' : '#BDC4DB';
  }

  get backgroundColor(){
    return this.theme === 'dark' ? '#1d2336' : '#eef0f5';
  }

};

decorate(AppStore, {
  theme: observable,
  nodeTextColor: computed,
  defaultStrokeColor: computed,
  backgroundColor: computed,
  changeTheme: action
});

export default new AppStore();