import React, { Component } from 'react';

import { observer, inject } from 'mobx-react';

import './rightMenu.css';

//import icons from "../../icons/icons.svg";
import { rmenuIconsLight, rmenuIconsDark } from '../../utils/rmenuIcons.js';

@inject("RightMenuStore", "AppStore")

@observer class RightMenu extends Component {
  render() {

    const rightMenuStore = this.props.RightMenuStore;
    const appStore = this.props.AppStore;
    
    const menuIcons = appStore.theme === 'dark' ? rmenuIconsLight : rmenuIconsDark;

  	return (
			<div className={ `right-menu ${appStore.theme} ${rightMenuStore.collapsed ? 'collapsed' : ''}` }>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon1} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon2} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon3} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon4} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon5} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon6} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon7} alt="" />
		    </div>
		    <div className="right-menu__item">
		     <img src={menuIcons.icon8} alt="" />
		    </div>
		    <div className="right-menu__item right-menu__more" onClick = {rightMenuStore.setCollapsed}>
		     <img src={menuIcons.icon9} alt="" />
		    </div>
			</div>
		);
  }
}

export default RightMenu;
