import * as go from 'gojs';
import HorizontalLink from './HorizontalLink.js';
import VerticalLink from './VerticalLink.js';

const $ = go.GraphObject.make;

class LinkTemplates {
  horizontal =
  $(HorizontalLink, {
          routing: go.Link.Orthogonal,
          corner: 13,
          fromEndSegmentLength: 20
        },
        $(go.Shape, {
            strokeWidth: 2,
            stroke: "#5a6487"
          },
          new go.Binding("stroke", "strokeColor")
        ),
        $(go.Shape, {
            fromArrow: "Circle",
            scale: 1.4,
            strokeWidth: 2,
            stroke: "#1d2336",
            fill: "#5a6487"
          },
          new go.Binding("fill", "startNodeColor"),
          new go.Binding("stroke", "backgroundColor")
        ),
        $(go.Shape, {
            toArrow: "Circle",
            scale: 1.4,
            strokeWidth: 2,
            stroke: "#1d2336",
            fill: "#5a6487"
          },
          new go.Binding("fill", "endNodeColor"),
          new go.Binding("stroke", "backgroundColor")
        ),
        $(go.TextBlock, "", 
          { 
            segmentOffset: new go.Point(-60, -12),
            segmentIndex: NaN,
            segmentFraction: 1,
            stroke: "#859DE0",
            font: "bold 14px Fira Sans" 
          },
          new go.Binding("text", "label"),
          new go.Binding("segmentOffset", "isFromRoot", (isFromRoot) => {
            return isFromRoot ? new go.Point(-80, -34) : new go.Point(-60, -12)
          }),
        ),
      );

  vertical =
  $(VerticalLink, {
          routing: go.Link.Orthogonal,
          corner: 12,
          fromEndSegmentLength: 20
        },
        $(go.Shape, {
            strokeWidth: 2,
            stroke: "#5a6487"
          },
          new go.Binding("stroke", "strokeColor")
        ),
        $(go.Shape, {
            fromArrow: "Circle",
            scale: 1.4,
            strokeWidth: 2,
            stroke: "#1d2336",
            fill: "#5a6487"
          },
          new go.Binding("fill", "startNodeColor"),
          new go.Binding("stroke", "backgroundColor")
        ),
        $(go.Shape, {
            toArrow: "Circle",
            scale: 1.4,
            strokeWidth: 2,
            stroke: "#1d2336",
            fill: "#5a6487"
          },
          new go.Binding("fill", "endNodeColor"),
          new go.Binding("stroke", "backgroundColor")
        ),
        $(go.TextBlock, "", 
          { 
            segmentOffset: new go.Point(-50, -27),
            segmentIndex: NaN,
            segmentFraction: 1,
            stroke: "#859DE0",
            font: "bold 14px Fira Sans" 
          },
          new go.Binding("text", "label"),
          new go.Binding("segmentOffset", "label", (label) => new go.Point(-50, -label.length*5))
        ),
      );
}

export default LinkTemplates;