import * as go from 'gojs';
import MyRoundedRectangle from './MyRoundedRectangle';

const $ = go.GraphObject.make;

go.Shape.defineFigureGenerator("MyRoundedRectangle", MyRoundedRectangle);

class NodeTemplates {

  constructor(riskCardStore, graphStore, infoCardStore){
    this.riskCardStore = riskCardStore;
    this.graphStore = graphStore;
    this.infoCardStore = infoCardStore;
  }

  isRiskClick = false;

  taxpayer =
    $(go.Node, "Auto", { width: 220, height: 64 },
      { selectionAdorned: false },
      $(go.Shape, "MyRoundedRectangle", 
        {
          width: 218, height: 62,
          fill: "transparent",
          strokeWidth: 2,
          parameter1: 32
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("fill", "isSelected", (isSel) => isSel ? '#859de033' : 'transparent').ofObject("")
      ),
      $(go.Panel, "Horizontal",
        { width: 220, height: 64 },
        $(go.Panel, "Vertical",
          { width: 72, height: 64, padding: 11 },
          $(go.Picture, 
            { margin: 3, width: 16, height: 16, background: "transparent" },
            new go.Binding("source", "valueIcon")
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "bold 14px Fira Sans"
            },
            new go.Binding("text", "BALANCE"),
            new go.Binding("stroke", "textColor")
          )
        ),
        $(go.Panel, "Vertical",
          { width: 176, height: 64, padding: 12, margin: -33 },
          new go.Binding("width", "isRisk", (isRisk) => isRisk ? 146 : 176),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "Title"),
            new go.Binding("stroke", "textColor")
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "INN"),
            new go.Binding("stroke", "textColor")
          )
        ),
        $(go.Panel, "Vertical",
          { width: 64, height: 64, padding: 10, margin: 3 },
          $(go.Picture, 
            { width: 40, height: 40, background: "transparent", cursor: "pointer" },
            new go.Binding("source", "riskIcon"),
            {
              click: (e, node) => {
 
                  this.graphStore.onRiscIconClick(node.panel.part);

              }
            }
          )
        ),
        $(go.Shape, "Circle", 
          {
            alignment: go.Spot.TopRight,
            width: 26, height: 26,
            fill: "#1d2336",
            strokeWidth: 0,
            margin: new go.Margin(-2, 0, 0, -26)
          },
          new go.Binding("visible", "isBalanceKr", (isBalanceKr) => isBalanceKr ? true : false),
          new go.Binding("fill", "backgroundColor")
        ),
        $(go.Picture, 
          { 
            alignment: go.Spot.TopRight, width: 20, height: 20, background: "transparent", 
            margin: new go.Margin(0, 0, 0, -22)
          },
          new go.Binding("source", "balanceIcon"),
          new go.Binding("visible", "isBalanceKr", (isBalanceKr) => isBalanceKr ? true : false),
        )
      ),
      {
        click: (e, node, a) => {
          if(!this.graphStore.isRiskClick){
            this.infoCardStore.setOpen(node);
            this.graphStore.changeCollapse(node);
          }
        }
      }
    );

  root =
    $(go.Node, "Auto", { width: 220, height: 64 },
      { selectionAdorned: false },
      $(go.Shape, "MyRoundedRectangle", 
        {
          width: 218, height: 62,
          fill: "transparent",
          stroke: "#5a6487",
          strokeWidth: 2,
          parameter1: 10
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("fill", "isSelected", (isSel) => isSel ? '#859de033' : 'transparent').ofObject("")
      ),
      $(go.Panel, "Horizontal",
        { width: 220, height: 64 },
        $(go.Panel, "Vertical",
          { width: 72, height: 64 },
          $(go.Picture, 
            { margin: new go.Margin(11, 0, 0, 0), width: 40, height: 40, background: "transparent" },
            new go.Binding("source", "flagIcon")
          )
        ),
        $(go.Panel, "Vertical",
          { width: 176, height: 64, padding: 12, margin: -25 },
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "CO_NAME"),
            new go.Binding("stroke", "textColor")
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "Title"),
            new go.Binding("stroke", "textColor")
          )
        )
      ),
      {
        click: (e, node) => {
          this.infoCardStore.setOpen(node);
          this.graphStore.changeCollapse(node);
        }
      }
    );

  tech =
    $(go.Node, "Auto", { width: 64, height: 64},
      { selectionAdorned: false },
      $(go.Shape, "MyRoundedRectangle", 
        {
          width: 54, height: 54,
          fill: "transparent",
          stroke: "#5a6487",
          strokeWidth: 2,
          parameter1: 10
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("margin", "", () => this.graphStore.direction === 0 ? new go.Margin(4, 0, 4, 0) : new go.Margin(0, 4, 0, 4)),
        new go.Binding("fill", "isSelected", (isSel) => isSel ? '#859de033' : 'transparent').ofObject("")
      ),
      $(go.Picture, 
        { margin: 7, width: 40, height: 40, background: "transparent" },
        new go.Binding("source", "image")
      ),
      {
        click: (e, node) => {
          this.infoCardStore.setOpen(node);
          this.graphStore.changeCollapse(node);
        }
      }
    );

  clone =
    $(go.Node, "Auto", { width: 220, height: 64 },
      { selectionAdorned: false },
      $(go.Shape, "MyRoundedRectangle", 
        {
          width: 218, height: 62,
          fill: "transparent",
          stroke: "#5a6487",
          strokeWidth: 2,
          parameter1: 32
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("fill", "isSelected", (isSel) => isSel ? '#859de033' : 'transparent').ofObject("")
      ),
      $(go.Panel, "Horizontal",
        { width: 220, height: 64 },
        $(go.Panel, "Vertical",
          { width: 72, height: 64 },
          $(go.Picture, 
            { margin: new go.Margin(18, 0, 0, 12), width: 26, height: 26, background: "transparent" },
            new go.Binding("source", "cloneIcon")
          )
        ),
        $(go.Panel, "Vertical",
          { width: 176, height: 64, padding: 12, margin: -33 },
          new go.Binding("width", "isRisk", (isRisk) => isRisk ? 146 : 176),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "Title"),
            new go.Binding("stroke", "textColor")
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: 3,
              stroke: "white",
              font: "14px Fira Sans"
            },
            new go.Binding("text", "INN"),
            new go.Binding("stroke", "textColor")
          )
        )
      ),
      {
        click: (e, node) => {
          this.infoCardStore.setOpen(node);
          this.graphStore.changeCollapse(node);
        }
      }
    );

  megaNode =
    $(go.Node, "Position", { width: 120, height: 64, alignment: go.Spot.Left },
      { selectionAdorned: false },
      $(go.Shape, "Circle", 
        {
          width: 62, height: 62,
          fill: "transparent",
          stroke: "#5a6487",
          strokeWidth: 2,
          margin: new go.Margin(0, 0, 0, 16),
          opacity: 0.2 
        },
        new go.Binding("margin", "oldCategory", (oldCategory) => {
          if(this.graphStore.direction === 0){
            return new go.Margin(0, 0, 0, 16);
          }else{
            if(oldCategory === 'tech'){
              return new go.Margin(16, 0, 0, 0);
            }else{
              return new go.Margin(16, 0, 0, 78);
            }
          }
        }),
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("fill", "backgroundColor")
      ),
      $(go.Shape, "Circle", 
        {
          width: 62, height: 62,
          fill: "transparent",
          stroke: "#5a6487",
          strokeWidth: 2,
          margin: new go.Margin(0, 0, 0, 8),
          opacity: 0.5 
        },
        new go.Binding("margin", "oldCategory", (oldCategory) => {
          if(this.graphStore.direction === 0){
            return new go.Margin(0, 0, 0, 8);
          }else{
            if(oldCategory === 'tech'){
              return new go.Margin(8, 0, 0, 0);
            }else{
              return new go.Margin(8, 0, 0, 78);
            }
          }
        }),
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("fill", "backgroundColor")
      ),
      $(go.Panel, "Auto",
        { width: 64, height: 64, margin: new go.Margin(0, 0, 0, 0) },
        new go.Binding("margin", "oldCategory", (oldCategory) => {
          if(this.graphStore.direction === 0){
            return new go.Margin(0, 0, 0, 0);
          }else{
            if(oldCategory === 'tech'){
              return new go.Margin(0, 0, 0, 0);
            }else{
              return new go.Margin(0, 0, 0, 78);
            }
          }
        }),
        $(go.Shape, "Circle", 
          {
            width: 62, height: 62,
            fill: "transparent",
            stroke: "#5a6487",
            strokeWidth: 2
          },
          new go.Binding("stroke", "strokeColor"),
          new go.Binding("fill", "backgroundColor")
        ),
        $(go.TextBlock, "Default Text", 
          {
            stroke: "white",
            font: "20px Fira Sans"
          },
          new go.Binding("text", "hiddenNodesCount"),
          new go.Binding("stroke", "textColor")
        )
      ),
      $(go.Panel, "Position", 
        { width: 32, height: 64, margin: new go.Margin(0, 0, 0, 84), alignment: go.Spot.Center },
        new go.Binding("margin", "oldCategory", (oldCategory) => {
          if(this.graphStore.direction === 0){
            return new go.Margin(0, 0, 0, 84);
          }else{
            if(oldCategory === 'tech'){
              return new go.Margin(84, 0, 0, 0);
            }else{
              return new go.Margin(84, 0, 0, 78);
            }
          }
        }),
        $(go.Panel, "Horizontal",
          { width: 32, height: 24, margin: new go.Margin(9, 0, 0, 0), alignment: go.Spot.Left },
          new go.Binding("margin", "", () => this.graphStore.direction === 0 ? new go.Margin(9, 0, 0, 0) : new go.Margin(0, 0, 0, 0)),
          $(go.TextBlock, "↓", 
            {
              stroke: "#859DE0",
              font: "bold 14px Fira Sans"
            }
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: new go.Margin(0, 0, 0, 4),
              stroke: "white",
              font: "bold 14px Fira Sans"
            },
            new go.Binding("text", "intoNodesCount"),
            new go.Binding("stroke", "textColor")
          )
        ),
        $(go.Panel, "Horizontal",
          { width: 32, height: 24, margin: new go.Margin(32, 0, 0, 0), alignment: go.Spot.Left },
          new go.Binding("margin", "", () => this.graphStore.direction === 0 ? new go.Margin(32, 0, 0, 0) : new go.Margin(0, 0, 0, 32)),
          $(go.TextBlock, "↑", 
            {
              stroke: "#5BB95B",
              font: "bold 14px Fira Sans"
            }
          ),
          $(go.TextBlock, "Default Text", 
            {
              margin: new go.Margin(0, 0, 0, 4),
              stroke: "white",
              font: "bold 14px Fira Sans"
            },
            new go.Binding("text", "outNodesCount"),
            new go.Binding("stroke", "textColor")
          ),
          new go.Binding("alignment", "", () => this.graphStore.direction === 0 ? go.Spot.Left : go.Spot.RightCenter),
        ),
        new go.Binding("width", "", () => this.graphStore.direction === 0 ? 32 : 64),
      ),
      new go.Binding("width", "oldCategory", (oldCategory) => {
        if(this.graphStore.direction === 0){
          return 120;
        }else{
          if(oldCategory === 'tech'){
            return 64;
          }else{
            return 220;
          }
        }
      }),
      new go.Binding("height", "", () => this.graphStore.direction === 0 ? 64 : 104),
      {
        click: (e, node) => {
          this.graphStore.openMegaNode(node);
        }
      }
    );
}

export default NodeTemplates;