import { observable, configure, action, decorate } from 'mobx';
import * as go from 'gojs';

import { getBackgroundColor } from '../utils/getColors.js';

configure({ enforceActions: 'observed' });

class RiskCardStore {

	constructor(appStore, graphStore){
    this.appStore = appStore;
    this.graphStore = graphStore;
  }

	diagram = null;

	isOpen = false;

	riskNode = null;
	riskInfo = [];
	risk = null;
	riskNum = null;
	connectedRntp = [];

	selectedNodes = [];

	openRiskCard = (node) => {
		this.isOpen = true;
		this.riskNode = node;
		this.risk = null;
		this.riskInfo = JSON.parse(node.RISK_INFO);
		//console.log(this.riskInfo);
	}

	setRiskNum = (riskNum) => {
		if(this.graphStore.selectedCollapseNode && this.graphStore.mode === 1){
      this.destroyCollapse();
    }
		this.riskNum = riskNum;
		this.creatRisk();
	}

	creatRisk = () => {
		this.destroyRisk(this.diagram);
		this.risk = this.riskInfo[this.riskNum];
		this.connectedRntp = this.risk ? this.risk.ConnectedRnpt || [] : [];
		if(this.risk && this.risk.ConnectedNodes){
      this.diagram.layout = new go.Layout();

      this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          const nodeIsConnected = this.risk.ConnectedNodes.filter((node) => node === data.ID).length > 0;

          if(nodeIsConnected && data.category !== 'clone'){
            this.diagram.findNodeForKey(data.key).opacity = 1;
            this.diagram.model.addLinkData({
              from: this.riskNode.key, 
              to: data.key,
              startNodeColor: this.riskNode.strokeColor,
				      fromDefaultColor: this.riskNode.isDefaultColor,
				      endNodeColor: data.strokeColor,
				      toDefaultColor: data.isDefaultColor,
				      strokeColor: '#8805ff',
     					backgroundColor: getBackgroundColor(this.appStore.theme),
              category: 'potential' 
            });
          }else if(this.riskNode.key === data.key){
            this.diagram.findNodeForKey(data.key).opacity = 1;
          }else{
            this.diagram.findNodeForKey(data.key).opacity = 0.1;
          }
        });

        m.linkDataArray.forEach((data) => {
          if(data.category !== 'potential'){
            this.diagram.findLinkForData(data).opacity = 0.1;
          }
        });

        //this.graphStore.addGraphEdges(this.riskCardStore.riskNode, risk.ConnectedNodes);
      });
    }
	}

	closeRisk = () => {
		this.destroyRisk(this.diagram);
		this.risk = null;
		this.connectedRntp = [];
		if(this.graphStore.selectedCollapseNode && this.graphStore.mode === 1){
      this.createCollapse();
    }
	}

	closeRiskCard = () => {
		this.isOpen = false;
		this.riskNode = null;
		this.riskInfo = [];
	}

	destroyRisk = (diagram) => {
		diagram.links.each((link) => {
			if(link.category === 'potential'){
         diagram.remove(link);
       }else{
         link.opacity = 1;
      }
		});
		diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          diagram.findNodeForKey(data.key).opacity = 1;
        });

      });
	}

};

decorate(RiskCardStore, {
	isOpen: observable,
	riskNode: observable,
	riskInfo: observable,
	risk: observable,
  openRiskCard: action,
  creatRisk: action,
  setRiskNum: action,
  closeRisk: action,
  closeRiskCard: action
});

export default RiskCardStore;