import React, { Component } from 'react';

import { observer, inject } from 'mobx-react';

import Draggable from 'react-draggable';

import iconClose from '../../icons/icon_close.svg';
import iconResort from '../../icons/icon_resort.svg';
import './riskCard.css';

@inject("RiskCardStore", "AppStore", "GraphStore")
@observer class RiskCard extends Component {

  riskCardStore = this.props.RiskCardStore;
  appStore = this.props.AppStore;
  graphStore = this.props.GraphStore;

  render() {

    const riskItems = this.graphStore.riskInfo.map((risk, i) => {
      return (
        <div 
          className="risk-menu__item" 
          key = { `risk_${i}` } 
          onClick = { () => this.graphStore.setRiskNum(i) }>{ risk.Name }
        </div>
      );
    });

    const RiskRntp = () => {
      return (
        <div className="risk__rntps">
          <div className="risk__rntps-title">
            {this.graphStore.connectedRntp.length > 0 ? 'Связанные РНТП:' : ''}
          </div>
          { riskRntpItems }
        </div>
      )
    }

    const riskRntpItems = this.graphStore.connectedRntp.map((rntp, i) => {
      return(
        <div key = { `rntp_${i}` }  className="risk__rntps-item">{ rntp.IPT }</div>
      )
    })

    //console.log(this.graphStore.risk);

    return (
      <Draggable>
      <div className = { `risk-menu ${ this.graphStore.riskCardIsOpen ? 'open' : 'close'} ${ this.appStore.theme }` }>
        <div className="risk-menu__close" onClick = { () => this.graphStore.closeRiskCard() }>
          <img src={iconClose} alt="" />
        </div>
        <div className="risk-menu__header">
          <div className="risk-menu__image">
            <img src={iconResort} alt="" />
          </div>
          <div className="risk-menu__info">
            <div className="risk-menu__title">
              {this.graphStore.riskNode ? this.graphStore.riskNode.SHORT_NAME : ''}
            </div>
          </div>
        </div>
        <div className="risk-menu__items">
          { riskItems }
        </div>
        <div className={ `risk ${ this.graphStore.risk ? 'open' : 'close'}` }>
          <div className="risk__close" onClick = { () => this.graphStore.closeRisk() }>
            <img src={iconClose} alt="" />
          </div>
          <div className="risk__header">
            <div className="risk__image">
              <img src={iconResort} alt="" />
            </div>
            <div className="risk__info">
              <div className="risk__title">
                {this.graphStore.risk ? this.graphStore.risk.Name : ''}
              </div>
              <div className="risk__nodetitle">
                {this.graphStore.riskNode ? this.graphStore.riskNode.SHORT_NAME : ''}
              </div>
            </div>
          </div>
          <div className="risk__inn">
            <span>ИНН:</span>{this.graphStore.riskNode ? this.graphStore.riskNode.INN : ''}
          </div>
          <RiskRntp/>
        </div>
      </div>
      </Draggable>
		);
  }
}

export default RiskCard;
