import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';

import Graph from '../graph';
import Menu from '../menu';
import RightMenu from '../rightMenu';
import RiskCard from '../riskCard';
import InfoCard from '../infoCard';
import Zoom from '../zoom';

import './app.css';

@inject("AppStore", "MenuStore")
@observer class App extends Component {

	diagramRef = React.createRef();

  componentDidMount(){
    this.diagram = this.diagramRef.current.getDiagram();
    //console.log(this.diagram);
  }

	render() { 
  	const AppStore = this.props.AppStore;
    
  	return (
  		<div className = { `app ${AppStore.theme === 'light' ? 'light' : 'dark'}` }>
  			<Menu/>
        <RightMenu/>
  			<Graph ref={this.diagramRef} /> 
  			<RiskCard diagram={this.diagram}/>
        <InfoCard/>
        <Zoom/>
  		</div>
  	)
  } 
}

export default App