import React, { Component } from 'react';

import { observer, inject } from 'mobx-react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css'

import './menu.css';

import icons from "../../icons/icons.svg";
import { menuIconsLight, menuIconsDark } from '../../utils/menuIcons.js';

@inject("AppStore", "GraphStore")

@observer class Menu extends Component {

	textInput = React.createRef();

	componentDidMount(){
		//this.textInput = React.createRef();
  }

  render() {

    const appStore = this.props.AppStore;
    const graphStore = this.props.GraphStore;
    
    const menuIcons = appStore.theme === 'dark' ? menuIconsLight : menuIconsDark;

    const options = [
		  { value: 0, label: 'Общий режим' },
		  { value: 1, label: 'Окружение НП по выбранному узлу'}
		];

	const defaultOption = options[graphStore.mode];

	const DropdownArrow = () => {
		return (
			<svg className="dropdown__arrow" width="10" height="7">
	      <use xlinkHref={ `${icons}#dropdown-arrow` }></use>
	    </svg>
		)
	}

  return (
		
	    <div className={ `header ${appStore.theme}` }>
		    <div className="header__left">
		      <div className="header__item">
		        <svg width="19" height="14">
	            	<use xlinkHref={ `${icons}#menu` }></use>
	         	</svg>
		      </div>
		      <div className="header__item">
		        <img src={menuIcons.iconMenu2} alt="" />
		      </div>
		      <div 
			      className={ `header__item ${graphStore.direction === 0 ? 'active' : ''}` } 
			      onClick = { graphStore.setHorizontalDirection }>
		        <img src={menuIcons.iconMenu3} alt="" />
		      </div>
		      <div 
		      	className={ `header__item ${graphStore.direction === 90 ? 'active' : ''}` } 
			      onClick = { graphStore.setVerticalDirection }>
		        <img src={menuIcons.iconMenu4} alt="" />
		      </div>
		    </div>
		    <div className="header__center">
		    	<Dropdown 
			    	options={options} 
			    	onChange={graphStore.changeMode} 
			    	value={defaultOption} 
			    	placeholder="Select an option" 
			    	arrowClosed={<DropdownArrow />}
	 					arrowOpen={<DropdownArrow />}
		    	/>
		    </div>
		    <div className="header__right">
		      <div className="header__item" onClick = { appStore.changeTheme }>
		        <img src={menuIcons.iconMenu5} alt="" />
		      </div>
		      <div className="header__item" onClick={ () => graphStore.setZoomToFit(100) }>
		        <img src={menuIcons.iconMenu6} alt="" />
		      </div>
		      <div className="header__item header__item-zoom" onClick={ () => this.textInput.current.focus() }>
		        <input 
		        	ref={this.textInput} 
		        	type="text" 
		        	value={graphStore.zoom} 
		        	onChange={ () => graphStore.setZoom(this.textInput.current.value) } 
		        />
		      </div>
		      <div className="header__item">
		        <img src={menuIcons.iconMenu8} alt="" />
		      </div>
		    </div>
		  </div>
		);
  }
}

export default Menu;
