import * as go from 'gojs';
import GraphStore from "../stores/GraphStore";
import AppStore from "../stores/AppStore";

class LayoutCompleted {

  constructor(myDiagram) {
    this.myDiagram = myDiagram;
  }

  graphStore = new GraphStore(AppStore);
  get rootNode() { return this.graphStore.rootNode };
  firstLevelNode = this.graphStore.firstLevelNode;

  commit = () => {
    //console.log(this.rootNode.key);
    this.myDiagram.commit((m) => {
      //m.set(node.data, "Title", '100');
      if (m.nodes.count) {
        m.nodes.each((node) => {
          if (node.data.key === this.rootNode.key) {
            node.location = new go.Point(node.location.x + 340, node.location.y - 300);
          }
          if (node.data.key === this.firstLevelNode.key) {
            node.location = new go.Point(node.location.x, node.location.y - 85);
          }
          if (node.category === 'tech') {
            node.location = new go.Point(node.location.x - 82, node.location.y);
          }
          if (node.category === 'megaNode') {
            node.location = new go.Point(node.location.x - 50, node.location.y);
          }
        });
      }
      if (m.links.count) {
        m.links.each((link) => {
          if (link.fromNode.key === this.rootNode.key) {
            //link.fromSpot.x = 0.1;
            //link.fromSpot.y = 1;
            //console.log(link.fromSpot);
          }
          //console.log(link.points, link.fromNode.key, link.fromNode.location);
        });
      }
    });
  }
}

export default LayoutCompleted;