import { observable, computed, configure, action, decorate } from 'mobx';

import * as go from 'gojs';

import { getBackgroundColor, getDefaultStrokeColor, getNodeStrokeColor, getGridColor } from '../utils/getColors.js';

import LinkTemplates from '../utils/LinkTemplates.js';
import NodeTemplates from '../utils/NodeTemplates.js';
import LayoutCompleted from '../utils/LayoutCompleted.js';
import { vertexes, edges } from '../utils/data.js';
import techIcons from '../utils/techIcons.js';
import valueIcons from '../utils/valueIcons.js';
import riskIcons from '../utils/riskIcons.js';
import balanceIcons from '../utils/balanceIcons.js';
import cloneIcons from '../utils/cloneIcons.js';
import flagIcons from '../utils/flagIcons.js';

configure({ enforceActions: 'observed' });

class GraphStore {

  constructor(appStore, riskCardStore, infoCardStore){
    this.appStore = appStore;
    this.riskCardStore = riskCardStore;
    this.infoCardStore = infoCardStore;
  }

  direction = 0;
  vertexes = vertexes;
  edges = edges;
  mode = 0;
  isRiskClick = false;
  
  countries = {
    '392': {
      SHORT_NAME: 'JP',
      FULL_NAME: 'Япония'
    }
  }

  get nodesWithClones(){
    const nodes = [ ...this.vertexes ];

    this.edges.forEach((edge) => {
      const startNode = nodes.filter((node) => node.PK === edge.SID)[0];
      const endNode = nodes.filter((node) => node.PK === edge.BID)[0];

      if (edge.SID === edge.BID || endNode.Level - startNode.Level > 1) {
        nodes.push({
          ...endNode,
          PK: endNode.PK + '_clone',
          category: 'clone',
          cloneIcon: cloneIcons.iconClone5A6487        });
      }
    });

    return nodes;
  }

  get edgesWithClones(){
    const edges = [ ...this.edges ];

    return edges.map((edge) => {
      const startNode = this.nodesWithClones.filter((node) => node.PK === edge.SID)[0];
      const endNode = this.nodesWithClones.filter((node) => node.PK === edge.BID)[0];

      if (edge.SID === edge.BID || endNode.Level - startNode.Level > 1) {
        return { ...edge, BID: edge.BID + '_clone' }
      }

      return { ...edge }
    });

  }

  get graphNodes() {
    return this.nodesWithClones.map((node) => {

      const mappedNode = { ...node, key: node.PK, opacity: 1 };

      if (+node.STATUS === 2 && +node.Level !== 0) {
        mappedNode.category = 'tech';      

        switch (node.TYPE) {
          case '24':
            mappedNode.image = techIcons.iconTech24;
            break;
          case '12':
            mappedNode.image = techIcons.iconTech12;
            break;
          case '20':
            mappedNode.image = techIcons.iconTech20;
            break;
          case '23':
            mappedNode.image = techIcons.iconTech23;
            break;
          case '18':
            //mappedNode.StrokeColor = 'icons/flag_japan.svg';
            break;
          default:
            //mappedNode.StrokeColor = 'icons/icon_tech_24.svg';
        }
      } else if (+node.STATUS === 2 && +node.Level === 0) {
        mappedNode.category = 'root';

        switch (node.CO_CODE) {
          case '392':
            mappedNode.flagIcon = flagIcons.iconFlagJapan;
            break;
          default:
            mappedNode.flagIcon = flagIcons.iconFlagJapan;
        }
      } else if(node.category !== 'clone') {
        mappedNode.category = 'taxpayer';

        if (node.BALANCE < 0) {
          mappedNode.valueIcon = valueIcons.iconValueF2696D;
        }else{
          mappedNode.valueIcon = valueIcons.iconValue5A6487;
        }

      }

      let maxTitleLength = 16;

      if (node.RISK_INFO) {
        mappedNode.riskIcon = riskIcons.iconResort;
        mappedNode.isRisk = true;
        maxTitleLength = 12;
      }else{
        mappedNode.isRisk = false;
      }
       
      mappedNode.isBalanceKr = true;

      if (node.BALANCE < 0 && node.BALANCE > -20000) {
        mappedNode.balanceIcon = balanceIcons.iconBalance;
      } else if (node.BALANCE < -20000) {
        mappedNode.balanceIcon = balanceIcons.iconBalanceKr;
      } else if (node.BALANCE_BR > 0) {
        mappedNode.balanceIcon = balanceIcons.iconBalancePos;
      }else{
        mappedNode.isBalanceKr = false;
      }

      mappedNode.Title = node.SHORT_NAME.length > maxTitleLength ? node.SHORT_NAME.slice(0, maxTitleLength) + '..' : node.SHORT_NAME;

      return mappedNode;
    });
  }

  get rootNode() {
    return this.graphNodes.filter((node) => +node.Level === 0)[0];
  }

  get firstLevelNode() {
    return this.graphNodes.filter((node) => +node.Level === 1)[0];
  }

  get graphEdges() {
    return this.edgesWithClones.map((edge) => {
      const startNode = this.graphNodes.filter((node) => node.PK === edge.SID)[0];

      const mappedEdge = { 
        ...edge,
        key: edge.PK,
        from: edge.SID,
        to: edge.BID,
        isFromRoot: false,
        reverse: false
      }

      let labelArrow = '↓';

      if (+startNode.STATUS === 2 && +startNode.Level !== 0) {
        labelArrow = '↑';
        mappedEdge.from = edge.BID;
        mappedEdge.to = edge.SID;
        mappedEdge.reverse = true;
      }

      if(+startNode.Level === 0){
        mappedEdge.isFromRoot = true;
      }

      mappedEdge.label = labelArrow + ' ' + edge.QUANTITY;

      return mappedEdge;
    });
  }

  linkTemplates = new LinkTemplates();

  get linkTemplate() {
    return this.direction === 0 ? this.linkTemplates.horizontal : this.linkTemplates.vertical;
  } 
  
  layerNum = 1;

  initDiagram = () => {
    const $ = go.GraphObject.make;
    const diagram =
      $(go.Diagram, {
        "undoManager.isEnabled": true,
        "animationManager.isEnabled": false,
        hasHorizontalScrollbar: false,
        hasVerticalScrollbar: false,
        //scrollMode: go.Diagram.InfiniteScroll,
        contentAlignment: this.grid.contentAlignment,
        //autoScale: go.Diagram.Uniform,
        padding: this.grid.padding,
        "grid.visible": true,
        allowDelete: false,
        allowMove: false,
        layout: $(go.LayeredDigraphLayout, 
          {
            setsPortSpots: false,
            direction: this.direction,
            layerSpacing: 100,
            columnSpacing: 14
          }),
      });

    diagram.grid =
      $(go.Panel, "Grid",
        { gridCellSize: this.grid.gridCellSize, gridOrigin: this.grid.gridOrigin },
        $(go.Shape, this.grid.shape, { 
          fill: getGridColor(this.appStore.theme), 
          interval: 2, 
          //width: this.grid.width,
          //height: this.grid.height
        })
      );

    // define node templates    
    const nodeTemplatesMap = new go.Map();
    const nodeTemplates = new NodeTemplates(this.riskCardStore, this, this.infoCardStore);

    diagram.nodeTemplate = nodeTemplates.taxpayer;

    nodeTemplatesMap.add("taxpayer", nodeTemplates.taxpayer);
    nodeTemplatesMap.add("root", nodeTemplates.root);
    nodeTemplatesMap.add("tech", nodeTemplates.tech);
    nodeTemplatesMap.add("clone", nodeTemplates.clone);
    nodeTemplatesMap.add("megaNode", nodeTemplates.megaNode);
    nodeTemplatesMap.add("", diagram.nodeTemplate);

    diagram.nodeTemplateMap = nodeTemplatesMap;

    diagram.linkTemplate = this.linkTemplate;

    var gridlayer = diagram.findLayer("Grid");
    diagram.addLayerBefore($(go.Layer, { name: "gridNumbers" }), gridlayer);

    $(go.Overview, "myOverviewDiv",  // the HTML DIV element for the Overview
      { observed: diagram, contentAlignment: go.Spot.Center });

    diagram.addDiagramListener("LayoutCompleted", (e) => {
      if(diagram.layout.direction === 0){
        return new LayoutCompleted(diagram).commit()
      }
    });

    /*diagram.addDiagramListener("InitialLayoutCompleted", (e) => {
      setTimeout(() => {
        return this.createGrid();
      }, 1000)
    });*/

    return diagram;
  }

  /*createGrid = () => {
    const $ = go.GraphObject.make;
    this.diagram.layout = new go.Layout();

    this.diagram.nodes.each((node) => {
      console.log(node.category);
      if(node.category === 'grid'){
        this.diagram.remove(node);
      }
    })

    var gradScaleHoriz =
      $(go.Node, "Graduated",
        { 
          graduatedTickUnit: 340, pickable: false, layerName: "Foreground",
          isInDocumentBounds: false, isAnimated: false
        },
        $(go.Shape, { geometryString: "M0 0 H400", stroke: '#fff' }),
        $(go.Shape, { geometryString: "M0 0 V3", interval: 1, stroke: '#fff' }),
        $(go.Shape, { geometryString: "M0 0 V15", interval: 5, stroke: '#fff' }),
        $(go.TextBlock, 
          { 
            font: "10px sans-serif",
            interval: 5,
            alignmentFocus: go.Spot.TopLeft,
            segmentOffset: new go.Point(0, 7), stroke: '#fff'
          }
        )
      );

      var vb = this.diagram.viewportBounds;
      gradScaleHoriz.location = new go.Point(vb.x, vb.y);
      gradScaleHoriz.graduatedMin = vb.x;
      gradScaleHoriz.graduatedMax = vb.right;
      gradScaleHoriz.scale = 1 / this.diagram.scale;

    this.diagram.add(gradScaleHoriz);
    console.log(this.diagram.viewportBounds, this.diagram.documentBounds)
  }*/

  handleModelChange = (changes) => {
    //alert('GoJS model changed!');
  }

  get grid() {
    return {
      contentAlignment: this.direction === 0 ? new go.Spot(0, 0.5) : new go.Spot(0.5, 0),
      padding: this.direction === 0 ? new go.Margin(0, 60) : new go.Margin(60, 0),
      shape: this.direction === 0 ? 'BarV' : 'BarH',
      gridCellSize:  this.direction === 0 ? new go.Size(340, 340) : new go.Size(184, 184),
      gridOrigin: this.direction === 0 ? new go.Point(-60, 0) : new go.Point(0, -60),
      width: this.direction === 0 ? 340 : '',
      height: this.direction === 0 ? '' : 184,
    } 
  }

  setGrid = () => {
    this.diagram.contentAlignment = this.grid.contentAlignment;
    this.diagram.padding = this.grid.padding;

    this.diagram.grid =
      go.GraphObject.make(go.Panel, "Grid",
        { gridCellSize: this.grid.gridCellSize, gridOrigin: this.grid.gridOrigin },
        go.GraphObject.make(go.Shape, this.grid.shape, { 
          fill: getGridColor(this.appStore.theme), 
          interval: 2, 
          //width: this.grid.width,
          //height: this.grid.height
        })
      );
  }

  setHorizontalDirection = () => {
    this.direction = 0;
    this.changeDirection();
  }

  setVerticalDirection = () => {
    this.direction = 90;
    this.changeDirection();
  }

  changeDirection = () => {
    if(this.risk){
      this.destroyRisk(this.diagram);
    }

    if(this.selectedCollapseNode && this.mode === 1){
      this.destroyCollapse();
    }

    this.setGrid();

    this.diagram.layout = 
      go.GraphObject.make(
        go.LayeredDigraphLayout, 
        {
          setsPortSpots: false,
          direction: this.direction,
          layerSpacing: 100,
          columnSpacing: 14
        }
      );
    this.diagram.layout.direction = this.direction;
    this.diagram.linkTemplate = this.linkTemplate;
    if(this.risk){
      setTimeout(() => {
        this.creatRisk();
      }, 10);
    }

    if(this.selectedCollapseNode && this.mode === 1 && !this.risk){
      setTimeout(() => {
        this.setCollapse();
      }, 10);
    }
  }

  changeTheme(){
    this.theme === 'light' ? this.theme = 'dark' : this.theme = 'light';
  }

  selectedCollapseNode = null;

  changeMode = (option) => {
    if(this.mode !== option.value){
      if(this.mode === 0){
        if(this.selectedCollapseNode && !this.risk){
          this.setCollapse();
        }
      }else{
        this.destroyCollapse();
      
        if(this.risk){
          this.creatRisk();
        }
      }

      this.mode = option.value;
    }
  }

  changeCollapse = (selectedNode) => {
    if(this.selectedCollapseNode && this.selectedCollapseNode.key === selectedNode.key && !this.risk){
      this.selectedCollapseNode = null;
      this.destroyCollapse();
    }else if(this.mode === 1 && !this.risk){
      //this.destroyCollapse();
      this.selectedCollapseNode = selectedNode;
      this.createCollapse();
    }
  }

  destroyCollapse = () => {
    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        if(node.category === 'megaNode'){
          node.category = node.data.oldCategory;
          d.model.set(node.data, "oldCategory", undefined);
          d.model.set(node.data, "strokeColor", getNodeStrokeColor(node.data, this.appStore.theme));
        }
        node.opacity = 1;
      });

      d.links.each((link) => {
        if(link.category === 'megaLink'){
          link.category = undefined;

          d.model.set(link.data, "label", link.data.oldLabel);
          d.model.set(link.data, "oldLabel", undefined);

          d.model.set(link.data, "endNodeColor", link.toNode.data.strokeColor);
          d.model.set(link.data, "startNodeColor", link.fromNode.data.strokeColor);
        }
        link.opacity = 1;
      });
    });
  }

  visibleNodes = [];

  createCollapse = () => {
    this.visibleNodes = [];

    this.diagram.nodes.each((currentNode) => {
      let isParent, isChildren = false;

      currentNode.findTreeParts().each((node) => {
        if(node.data.key === this.selectedCollapseNode.data.key){
          isParent = true;
        }
      });

      currentNode.findNodesInto().each((node) => {
        if(node.data.key === this.selectedCollapseNode.data.key){
          isChildren = true;
        }
      })

      if(isParent || isChildren || currentNode.category === 'root'){
        this.visibleNodes.push(currentNode.data.key);
      }
    });

    this.setCollapse();
  }

  openMegaNode = (node) => {

    node.findLinksInto().each((link) => {
      if(link.category === 'megaLink'){
        link.fromNode.findNodesOutOf().each((node) => {
          this.visibleNodes.push(node.data.key);
        });
        link.fromNode.findNodesInto().each((node) => {
          if(node.findNodesInto().count === 0){
            this.visibleNodes.push(node.data.key);
          }
        });
      }
    });

    this.setCollapse();
  }

  setCollapse = () => {
    this.destroyCollapse();
    this.diagram.layout = new go.Layout();
    const nodeIsVisible = (node) => this.visibleNodes.some((nodeKey) => nodeKey === node.key);

    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        if(!nodeIsVisible(node)){
          node.opacity = 0

          node.findLinksConnected().each((link) => {
            link.opacity = 0
          });
        }
      });

      this.visibleNodes.forEach((nodeKey) => {
        const intoHiddenNodes = [];
        const outHiddenNodes = [];

        const currentNode = d.findNodeForKey(nodeKey);

        currentNode.findNodesInto().each((node) => {
          if(!nodeIsVisible(node) || node.category === 'megaNode'){
            intoHiddenNodes.push(node);
          }
        });

        currentNode.findNodesOutOf().each((node) => {
          let isReverse = false;

          node.findLinksBetween(currentNode).each((link) => {
            if(link.data.reverse) isReverse = true;
          });

          if(!nodeIsVisible(node) && !isReverse){
            outHiddenNodes.push(node);
          }else if(!nodeIsVisible(node) && isReverse){
            intoHiddenNodes.push(node);
          }
        });

        //console.log(currentNode.data.SHORT_NAME, intoHiddenNodes);

        if(outHiddenNodes.length > 0){
          let megaIndex = Math.ceil((outHiddenNodes.length)/2) - 1;
          //if(outHiddenNodes[megaIndex].findNodesInto().count > 1) megaIndex++;
          const megaNode = outHiddenNodes[megaIndex];
          const hiddenNodesCount = outHiddenNodes.length + intoHiddenNodes.length;

          d.model.set(megaNode.data, "oldCategory", megaNode.category);
          megaNode.category = 'megaNode';

          d.model.set(megaNode.data, "strokeColor", getDefaultStrokeColor(this.appStore.theme));

          megaNode.opacity = 1;
          
          //d.model.set(megaNode.data, "STATUS", 100)
          d.model.set(megaNode.data, "hiddenNodesCount", hiddenNodesCount);
          d.model.set(megaNode.data, "intoNodesCount", intoHiddenNodes.length);
          d.model.set(megaNode.data, "outNodesCount", outHiddenNodes.length);
          
          currentNode.findLinksTo(outHiddenNodes[megaIndex]).each((link) => {
            link.category = 'megaLink';

            d.model.set(link.data, "oldLabel", link.data.label);
            d.model.set(link.data, "label", '');

            d.model.set(link.data, "endNodeColor", getDefaultStrokeColor(this.appStore.theme));

            link.opacity = 1;
          });
        }    
      });
    });
  } 

  diagram = null;

  riskCardIsOpen = false;

  riskNode = null;
  riskInfo = [];
  risk = null;
  riskNum = null;
  connectedRntp = [];

  selectedNodes = [];

  openRiskCard = (node) => {
    this.riskCardIsOpen = true;
    this.riskNode = node;
    this.risk = null;
    this.riskInfo = JSON.parse(node.RISK_INFO);
    //console.log(this.riskInfo);
  }

  closeRiskCard = () => {
    this.riskCardIsOpen = false;
    this.riskNode = null;
    this.riskInfo = [];
  }

  onRiscIconClick = (node) => {
    this.isRiskClick = true;
    if(this.riskCardIsOpen && this.riskNode.key === node.key){
      this.closeRiskCard();
    }else{
      this.openRiskCard(node.data);
    }
    setTimeout(() => {
      this.isRiskClick = false;
    }, 100);
  }

  setRiskNum = (riskNum) => {
    if(this.selectedCollapseNode && this.mode === 1){
      this.destroyCollapse();
    }
    this.riskNum = riskNum;
    this.creatRisk();
  }

  creatRisk = () => {
    if(this.selectedCollapseNode && this.mode === 1){
      this.destroyCollapse();
    }
    this.destroyRisk();
    this.risk = this.riskInfo[this.riskNum];
    this.connectedRntp = this.risk ? this.risk.ConnectedRnpt || [] : [];
    if(this.risk && this.risk.ConnectedNodes){
      this.diagram.layout = new go.Layout();

      this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          const nodeIsConnected = this.risk.ConnectedNodes.filter((node) => node === data.ID).length > 0;

          if(nodeIsConnected && data.category !== 'clone'){
            this.diagram.findNodeForKey(data.key).opacity = 1;
            this.diagram.model.addLinkData({
              from: this.riskNode.key, 
              to: data.key,
              startNodeColor: this.riskNode.strokeColor,
              fromDefaultColor: this.riskNode.isDefaultColor,
              endNodeColor: data.strokeColor,
              toDefaultColor: data.isDefaultColor,
              strokeColor: '#8805ff',
              backgroundColor: getBackgroundColor(this.appStore.theme),
              category: 'potential' 
            });
          }else if(this.riskNode.key === data.key){
            this.diagram.findNodeForKey(data.key).opacity = 1;
          }else{
            this.diagram.findNodeForKey(data.key).opacity = 0.1;
          }
        });

        m.linkDataArray.forEach((data) => {
          if(data.category !== 'potential'){
            this.diagram.findLinkForData(data).opacity = 0.1;
          }
        });

        //this.graphStore.addGraphEdges(this.riskCardStore.riskNode, risk.ConnectedNodes);
      });
    }
  }

  closeRisk = () => {
    this.destroyRisk();
    this.risk = null;
    this.connectedRntp = [];
    if(this.selectedCollapseNode && this.mode === 1){
      this.setCollapse();
    }
  }

  destroyRisk = () => {
    this.diagram.links.each((link) => {
      if(link.category === 'potential'){
         this.diagram.remove(link);
       }else{
         link.opacity = 1;
      }
    });
    this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          this.diagram.findNodeForKey(data.key).opacity = 1;
        });

      });
  }

  zoom = 100;

  setZoom = (value) => {
    value = parseInt(value);
    if(value < 1) value = 1;
    if(value > 200) value = 200;
    this.zoom = value;
    this.diagram.scale = this.zoom/100;
  }

  setZoomToFit = () => {
    this.diagram.zoomToFit();
    this.zoom = this.diagram.scale * 100;
  }

};

decorate(GraphStore, {
  vertexes: observable,
  edges: observable,
  direction: observable,
  mode: observable,
  graphNodes: computed,
  graphEdges: computed,
  rootNode: computed,
  firstLevelNode: computed,
  linkTemplate: computed,
  initDiagram: action,
  handleModelChange: action,
  changeDirection: action,
  changeLinkTypes: action,
  setGrid: action,
  setCollapse: action,
  changeCollapse: action,
  selectedCollapseNode: observable,
  changeMode: action,
  riskCardIsOpen: observable,
  riskNode: observable,
  riskInfo: observable,
  risk: observable,
  openRiskCard: action,
  creatRisk: action,
  setRiskNum: action,
  closeRisk: action,
  closeRiskCard: action,
  onRiscIconClick: action,
  visibleNodes: observable,
  setHorizontalDirection: action,
  setVerticalDirection: action,
  zoom: observable,
  setZoom: action,
  setZoomToFit: action
});

export default GraphStore;