import React, { Component } from 'react';

import { observer, inject } from 'mobx-react';
//import * as go from 'gojs';
import { ReactDiagram } from 'gojs-react';
import { getBackgroundColor, getDefaultStrokeColor, getNodeStrokeColor, getTextColor } from '../../utils/getColors.js';

import './graph.css';

@inject("GraphStore", "AppStore", "RiskCardStore")

@observer class Graph extends Component {

  diagramRef = this.props.innerRef;
  graphStore = this.props.GraphStore;
  appStore = this.props.AppStore;
  riskCardStore = this.props.RiskCardStore;

  componentDidMount(){
    this.diagram = this.diagramRef.current.getDiagram();
    this.graphStore.diagram = this.diagram;
    this.riskCardStore.diagram = this.diagram;
  }

  componentDidUpdate(){
    this.graphStore.setGrid();

    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        d.model.set(node.data, "strokeColor", getNodeStrokeColor(node.data, this.appStore.theme));
        d.model.set(node.data, "textColor", getTextColor(this.appStore.theme));
        d.model.set(node.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
      });

      d.links.each((link) => {
        //if(link.data.fromDefaultColor){
          d.model.set(link.data, "startNodeColor", link.fromNode.data.strokeColor);
        //}

        //if(link.data.toDefaultColor){
          d.model.set(link.data, "endNodeColor", link.toNode.data.strokeColor);
        //}

        if(link.data.category !== 'potential'){
          d.model.set(link.data, "strokeColor", getDefaultStrokeColor(this.appStore.theme));
        }
        d.model.set(link.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
      });
    });
  } 

  graphNodes = this.graphStore.graphNodes.map((node) => {
    const strokeColor = getNodeStrokeColor(node, this.appStore.theme);
    const defaultStrokeColor = getDefaultStrokeColor(this.appStore.theme);

    return { 
      ...node,
      strokeColor: strokeColor,
      textColor: getTextColor(this.appStore.theme),
      backgroundColor: getBackgroundColor(this.appStore.theme),
      isDefaultColor: strokeColor === defaultStrokeColor ? true : false
    };
  });
  

  graphEdges = this.graphStore.graphEdges.map((edge) => {
    const startNode = this.graphNodes.filter((node) => node.PK === edge.SID)[0];
    const endNode = this.graphNodes.filter((node) => node.PK === edge.BID)[0];

    const mappedEdge = { 
      ...edge,
      startNodeColor: startNode.strokeColor,
      //fromDefaultColor: startNode.isDefaultColor,
      endNodeColor: endNode.strokeColor,
      //toDefaultColor: endNode.isDefaultColor,
      strokeColor: getDefaultStrokeColor(this.appStore.theme),
      backgroundColor: getBackgroundColor(this.appStore.theme)
    }

    if (+startNode.STATUS === 2 && +startNode.Level !== 0) {
      mappedEdge.startNodeColor = endNode.strokeColor;
      mappedEdge.endNodeColor = startNode.strokeColor;
      //mappedEdge.fromDefaultColor = endNode.isDefaultColor;
      //mappedEdge.toDefaultColor = startNode.isDefaultColor;
    }

    return mappedEdge;
  });
  

  render() {
    return (
			<div>
        <ReactDiagram
          divClassName={ 'diagram-component ' + this.appStore.theme }
          ref={this.diagramRef}
          initDiagram={this.graphStore.initDiagram }
          nodeDataArray={this.graphNodes}
          linkDataArray={this.graphEdges}
          onModelChange={this.graphStore.handleModelChange}
        />
      </div>
		);
  }
}

export default React.forwardRef((props, ref) => <Graph innerRef={ref} {...props} />);
