const getBackgroundColor = (theme) => theme === 'dark' ? '#1d2336' : '#eef0f5';

const getGridColor = (theme) => theme === 'dark' ? '#181e2f' : '#f2f4f7';

const getDefaultStrokeColor = (theme) => theme === 'dark' ? '#5a6487' : '#BDC4DB';

const getNodeStrokeColor = (node, theme) => {

	let strokeColor = getDefaultStrokeColor(theme);

    if (node.category === 'tech') {
        switch (node.TYPE) {
          case '24':
            strokeColor = '#5BB95B';
            break;
          case '12':
            strokeColor = '#31ABC4';
            break;
          case '20':
            strokeColor = '#859DE0';
            break;
          case '23':
            strokeColor = '#F09456';
            break;
          case '18':
            //mappedNode.StrokeColor = 'icons/flag_japan.svg';
            break;
          default:
            //strokeColor = defaultStrokeColor;
        }
      } else if (node.category === 'root') {
        //strokeColor = defaultStrokeColor;
      } else if (node.category === 'taxpayer'){
        if (node.BALANCE < 0) {
          strokeColor = '#F2696D';
        }else{
          //strokeColor = defaultStrokeColor;
        }
      }

      return strokeColor;
  }

  const getTextColor = (theme) => {
    return theme === 'dark' ? '#ffffff' : '#2b3250';
  }

  export { getBackgroundColor, getGridColor, getDefaultStrokeColor, getNodeStrokeColor, getTextColor };