import React, { Component } from 'react';

import { observer, inject } from 'mobx-react';
import { graphInfo } from '../../utils/data.js';
import { Scrollbars } from 'react-custom-scrollbars';

import './infoCard.css';

import inconArrow from '../../icons/icon_arrow.svg';

@inject("InfoCardStore", "AppStore", "GraphStore")

@observer class InfoCard extends Component {
	render() {

		const infoCardStore = this.props.InfoCardStore;
		const appStore = this.props.AppStore;
		const graphStore = this.props.GraphStore;
		
		const open = infoCardStore.open ? 'open' : '';
		const collapsed = infoCardStore.collapsed ? 'collapsed' : ''

		const node = infoCardStore.selectedNode;

		const cardDataItem = (item) => {	
			if(node && node.data[item]){
				return node.data[item];
			}else{
				return null;
			}
		}

		const GenCardItem = ({ title, value, unit }) => {
			if(node && value){
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value } { unit ? unit : null }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		const CardItem = ({ title, value }) => {
			if(node && node.data[value]){
				if(value === 'ROOT' || value === 'LEAF'){
					value = node.data[value] === 'true' ? 'Да' : 'Нет';
				}else{
					value = node.data[value];
				}

				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		return (
			<div className={ `info-card ${appStore.theme} ${open} ${collapsed}` }>
		    <Scrollbars autoHide style={{ height: '100%' }}>
		    <div className="info-card__close" onClick = { infoCardStore.changeCollapsed }>
		      <img src={inconArrow} alt="" />
		    </div>
		    <div className="info-card__show" onClick = { infoCardStore.changeCollapsed }>
		      Развернуть
		    </div>
		    <div className="info-card__header">
		      <div className="info-card__subtitle">
		        Наименование
		      </div>
		      <div className="info-card__title">
		      	{ cardDataItem('SHORT_NAME') }
		      	<br/>
		      	{ cardDataItem('INN') }
		      </div>
		    </div>
		    <div className="info-card__info">
		      <div className="info-card__info-item">
		      	<span>РНПТ:</span> { graphInfo.IPT }
		      </div>
		      <div className="info-card__info-item">
		      	<span>ИНН\КПП:</span> { cardDataItem('INN') }\{ cardDataItem('KPP') }
		      </div>
		    </div>
		    <div className="info-card__section">
		      <div className="info-card__section-title">
		        Общие сведения
		      </div>
		      <GenCardItem title='РНПТ' value={ graphInfo.IPT }/>
		      <GenCardItem title='Количество товара' value={ graphInfo.IMP_CNT } unit='ед.'/>
		      <GenCardItem title='Цена товара' value={ graphInfo.IMP_COST } unit='руб.'/>
		      <GenCardItem title='Страна отправления' value={ graphStore.countries[graphInfo.ORIG_CO_CODE].FULL_NAME }/>
		    </div>
		    <div className="info-card__section">
		      <div className="info-card__section-title">
		        Сведения о НП по РНПТ
		      </div>
		      <CardItem title='Режим НО' value='TAX_TYPE_NAME' />
		      <CardItem title='Страна' value='CO_NAME' />
		      <CardItem title='Корневой' value='ROOT' />
		      <CardItem title='Окончание графа' value='LEAF' />
		      <CardItem title='Остаток' value='BALANCE' />
		      <CardItem title='Остаток(посредник)' value='BALANCE_BR' />
		      <CardItem title='Коды ОКПД2' value='OKPD2_LIST' />
		      <CardItem title='Код ТН ВЭД' value='TNVED' />
		    </div>
		    <a href="/" className="info-card__button">Операции на узле</a>
		     </Scrollbars>
		  </div>
		);
	}
}

export default InfoCard;
